import { deepClone } from '@/assets/js/deepClone.js';
export default {
	name: 'computedMedia',
	components: { Header },
	data() {
		return {
			oldStyle: `/*
â€œæ‰‹æœºç«¯ä»£ç ç”Ÿæˆå™¨â€æ˜¯å°†pcç«¯ä»£ç ä¸­çš„pxé€šè¿‡å¯è‡ªå®šä¹‰çš„è®¡ç®—è§„åˆ™ç”Ÿæˆå¯ä¾›æ‰‹æœºç«¯ä½¿ç”¨çš„ä»£ç 

æç¤ºï¼š
1.å³ä¸Šè§’ã€è®¾ç½®è®¡ç®—è§„åˆ™ã€‘æŒ‰é’®è‡ªå®šä¹‰è®¡ç®—è§„åˆ™
2.ç”Ÿæˆåçš„æ ·å¼å¯èƒ½ä¼šå½±å“å¸ƒå±€
3.å»ºè®®æ¯æ¬¡å°‘æ®µè½¬æ¢å¹¶æ ¸éªŒ
4.ç¾åŒ–ä»£ç è¶…è¿‡2000è¡Œä¼šä¸¥é‡å½±å“æ•ˆç‡ï¼Œä¼šè‡ªåŠ¨å…³é—­ç¾åŒ–åŠŸèƒ½ï¼Œå¯æ‰‹åŠ¨å¼€å¯
*/

/*ç¤ºä¾‹ï¼šâ†“â†“â†“*/
.list li {
    border-bottom: 10px solid #999;
    border-bottom: 2px solid #999;
    border-radius: 10px;
    border-radius: 5px;
    border-radius: 100%;
    background: #409EFF;
