}

.list {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
    margin: 30px auto 5%;
    padding: 500px 10%;
    width: 20px;
    height: 20px;

    font-size: 30px;
    font-size: 20px;
    font-size: 12px;
    transform: translateY(-50px);

    position: absolute;
    left: 10px;
    top: calc(100% - 10px);
    display: flex;
    gap: 20px 60px;
    border-width: 20px;
}`,

			collapseActive: ['1', '2'], //æŠ˜å é¢æ¿

			showConfig: true, //æ˜¾ç¤ºé…ç½®
			showConfig: false, //æ˜¾ç¤ºé…ç½®
			tipShow: false, //æ˜¾ç¤ºæç¤º
			newStyle: '',
			firstCloseBeautify: true,
			firstVisit: true, //ç¬¬ä¸€æ¬¡è®¿é—®
			beautifyMax: 2000,
			history: null,
			defaultConfig: null, //ç”¨äºæ‹·è´ä¸€ä»½åˆå§‹æ•°æ®
			config: {
				isBeautify: true,
				autoCopy: true,
				saveConfig: true,
				decimal: false,
				testExp: false,
			},
			tableData: [
				{
					name: 'åœ†è§’',
					attr: 'border-radius',
					styleEnsample: 'border-radius: 30px;',
					isCalc: true,
					multiplier: 0.5,
					min: 10,
					max: 9999,
					message: 'ç™¾åˆ†æ¯”ç±»å‹ä¸å‚ä¸è®¡ç®— border-radius:100%;',
				},
				{
					name: 'è¾¹æ¡†',
					attr: 'border',
					styleEnsample: 'border-bottom: 10px solid #999;',
					isCalc: true,
					multiplier: 0.5,
					min: 2,
					max: 9999,
					message: '',
				},
				{
					name: 'box-shadow',
